
import { defineComponent, toRefs, ref, onMounted, h } from 'vue'
import UseCodeEditor from './UseCodeEditor'
import UseJsonEditor from './UseJsonEditor'
import { notification } from 'ant-design-vue'

export default defineComponent({
  name: '首页',
  setup () {
    const {
      isErrorJson,
      foldJson,
      getJsonEditorValue,
      jsonOption,
      changeConfig
    } = UseJsonEditor('JSON-EDITOR')

    const isShowConfig = ref(false)

    const { setCode, clearCode } = UseCodeEditor('CODE-EDITOR')

    const setData = () => {
      const jsonStr = getJsonEditorValue()
      setCode(jsonStr)
    }

    const clear = () => {
      setCode('')
    }

    const openConfig = () => {
      isShowConfig.value = true
    }

    const closeConfig = () => {
      isShowConfig.value = false
      changeConfig()
    }

    onMounted(() => {
      const props = {
        href: 'http://blog.lynn.cool/',
        target: '_blank'
      }
      notification.open({
        message: 'tips',
        description: h('a', props, '前往http://blog.lynn.cool/查看更多小工具'),
        placement: 'topRight',
        duration: 2
      })
    })

    return {
      isErrorJson,
      setData,
      foldJson,
      clear,
      clearCode,
      isShowConfig,
      changeConfig,
      openConfig,
      ...toRefs(jsonOption),
      closeConfig
    }
  }
})
